import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getYearEducations } from 'services/yearEducationService';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { getSemesters } from 'services/semesterService';
import { getReceivable } from 'services/receivable';
import './style.scss';

const { Option } = Select;

@Form.create({ name: 'FormSearchClasses' })
class FormSearchStudent extends Component {
  state = {
    receivables: [],
    selectedReceivables: [],
  };

  componentDidMount = () => {
    const { form } = this.props;
    const params = getQueryParams();
    delete params._time;
    const { receivableIds } = params;
    delete params.receivableIds;
    if (params.yearEduId) params.yearEduId = parseInt(params.yearEduId);
    form.setFieldsValue(params);
    this.getReceivable();
    if (receivableIds) {
      this.setState({
        selectedReceivables: receivableIds.split('and').map(receivableId => parseInt(receivableId)),
      });
    }
  };

  getReceivable = async () => {
    const res = await getReceivable();
    const receivables = res.data.items
      .filter(item => !item.isFee && !item.isTrainExpense)
      .map(item => {
        const option = {
          label: item.label,
          value: item.id,
        };
        return option;
      });
    this.setState({ receivables });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      const { selectedReceivables } = this.state;
      delete values.receivables;
      updateQueryParams({ ...values, receivableIds: selectedReceivables.join('and') });
    });
  };

  handleSelectReceivables = receivableIds => {
    this.setState({ selectedReceivables: receivableIds });
  };

  render() {
    const {
      form: { getFieldDecorator, getFieldValue, setFieldsValue },
    } = this.props;
    const { receivables, selectedReceivables } = this.state;
    let yearEduId = getFieldValue('yearEduId');
    yearEduId = yearEduId ? yearEduId.toString() : yearEduId;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
    };

    return (
      <Form
        className="form-inline-label search-form"
        layout="vertical"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24} type="flex" align="stretch">
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col xs={24} sm={12} md={5} lg={5} xl={5}>
            <Form.Item label="Năm học" required>
              {getFieldDecorator('yearEduId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={5} lg={5} xl={5}>
            <Form.Item label="Học kỳ" required>
              {getFieldDecorator('semesterId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={10}>
            <Form.Item label="Loại phí tổng hợp" required>
              {getFieldDecorator('receivables', {
                initialValue: selectedReceivables,
                valuePropName: 'value',
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select
                  placeholder="Chọn loại phí tổng hợp"
                  mode="multiple"
                  onChange={this.handleSelectReceivables}
                >
                  {receivables.map(item => (
                    <Option key={item.value} value={item.value}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



