import React from 'react';
import { Row, Col } from 'antd';
import { numberToMoney } from 'utils/formatUtil';
import './style.scss';

export const StatisticBar = props => (
  <Row gutter={24} className="statistic-bar" type="flex" align="stretch">
    <Col span={24} className="statistic-bar-content">
      <Col xs={24} sm={24} md={8} lg={8} xl={7}>
        <Row>
          <Col span={18} className="title">
            Số tiền phải thu kỳ:
          </Col>
          <Col span={6} className="result">
            {numberToMoney(props.totalInformation && props.totalInformation.sumMustPayCurrPeriod)}
          </Col>
          <Col span={18} className="title">
            Số tiền đã thu kỳ:
          </Col>
          <Col span={6} className="result">
            {numberToMoney(props.totalInformation && props.totalInformation.sumPaidCurrPeriod)}
          </Col>
        </Row>
      </Col>
      <Col xs={24} sm={24} md={8} lg={8} xl={7}>
        <Row>
          <Col span={18} className="title">
            Số tiền thu thừa kỳ:
          </Col>
          <Col span={6} className="result">
            {numberToMoney(props.totalInformation && props.totalInformation.sumOverPaidAmount)}
          </Col>
          <Col span={18} className="title">
            Số tiền còn phải thu kỳ:
          </Col>
          <Col span={6} className="result">
            {numberToMoney(props.totalInformation && props.totalInformation.sumRemainAmount)}
          </Col>
        </Row>
      </Col>
      <Col xs={24} sm={24} md={8} lg={8} xl={7}>
        <Row>
          <Col span={18} className="title">
            Số tiền thu thừa cuối kỳ:
          </Col>
          <Col span={6} className="result">
            {numberToMoney(props.totalInformation && props.totalInformation.sumFinalOverPaid)}
          </Col>
          <Col span={18} className="title">
            Số tiền còn phải thu cuối cùng:
          </Col>
          <Col span={6} className="result">
            {numberToMoney(props.totalInformation && props.totalInformation.sumFinalRemainAmount)}
          </Col>
        </Row>
      </Col>
      <Col xs={24} sm={24} md={4} lg={4} xl={3}>
        <Row style={{ height: '100%' }}>
          <Col span={18} className="title">
            Tổng số SV:
          </Col>
          <Col span={6} className="result">
            {props.totalStudents}
          </Col>
        </Row>
      </Col>
    </Col>
  </Row>
);



