import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnExport } from 'components/common/button';
import { Card, Row, Col } from 'antd';
import { CartHeadAction, TableData } from 'components/common';
import { numberToMoney } from 'utils/formatUtil';
import { getTuitionDeptChargeStatistic, doExport } from 'services/tuitionDeptChargeStatistic';
import { getQueryParams } from 'utils/urlUtil';
import FormSearchStudent from './FormSearchStudent';
import { StatisticBar } from './StatisticBar';
import './style.scss';

class TuitionDeptChargeStatisticPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
    totalInformation: {},
    totalStudents: 0,
    downloading: false,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onSelectAllForAddStudent = (selectedStudents, isSelect) => {
    if (isSelect) {
      const addingStudents = {};
      selectedStudents.forEach(student => {
        addingStudents[student.studentId] = student;
      });
      this.setState({ addingStudents });
    } else {
      this.setState({ addingStudents: {} });
    }
  };

  onSelectForRemoveStudent = (selectedStudent, isSelect) => {
    const deletingStudents = { ...this.state.deletingStudents };
    if (isSelect) {
      deletingStudents[selectedStudent.studentId] = selectedStudent;
    } else {
      delete deletingStudents[selectedStudent.studentId];
    }
    this.setState({
      deletingStudents,
    });
  };

  onSelectAllForRemoveStudent = (selectedStudents, isSelect) => {
    if (isSelect) {
      const deletingStudents = {};
      selectedStudents.forEach(student => {
        deletingStudents[student.studentId] = student;
      });
      this.setState({ deletingStudents });
    } else {
      this.setState({ deletingStudents: {} });
    }
  };

  onAddStudents = () => {
    const addingStudentsTableData = { ...this.state.addingStudentsTableData };

    Object.entries(this.state.addingStudents).forEach(([key, value]) => {
      addingStudentsTableData[key] = value;
    });
    this.setState({ addingStudentsTableData });
  };

  getTuitionDeptChargeStatistic = async params => {
    const res = await getTuitionDeptChargeStatistic(params);
    const { information, totalResults } = res.data;
    this.setState({ totalInformation: information, totalStudents: totalResults });
    return res;
  };

  handleChangeQueryParams = () => {
    const { receivableIds } = getQueryParams();
    const receivableIdsArr =
      receivableIds && receivableIds.length > 0 ? receivableIds.split('and') : [];
    this.setState({
      paramSearchClass: { ...getQueryParams(), receivableIds: receivableIdsArr },
    });
  };

  doExport = async () => {
    console.log(this.state.paramSearchClass);
    const { paramSearchClass } = this.state;
    this.setState({ downloading: true });
    try {
      await doExport(paramSearchClass);
    } finally {
      this.setState({ downloading: false });
    }
  };

  render() {
    const { reload, paramSearchClass, totalInformation, totalStudents, downloading } = this.state;
    const isBtnExportDisable = totalStudents <= 0;
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'studentCode',
        key: 'studentCode',
        align: 'center',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
        align: 'center',
      },
      {
        title: 'Nợ kỳ trước',
        dataIndex: 'prevPeriodFeeDebt',
        key: 'prevPeriodFeeDebt',
        align: 'center',
        render: value => numberToMoney(value),
      },
      {
        title: 'Phải nộp kỳ',
        dataIndex: 'mustPayCurrPeriod',
        key: 'mustPayCurrPeriod',
        align: 'center',
        render: value => numberToMoney(value),
      },
      {
        title: 'Đã nộp kỳ',
        dataIndex: 'paidCurrPeriod',
        key: 'paidCurrPeriod',
        align: 'center',
        render: value => numberToMoney(value),
      },
      {
        title: 'Hoàn trả kỳ',
        dataIndex: 'returnAmount',
        key: 'returnAmount',
        align: 'center',
        render: value => numberToMoney(value),
      },

      {
        title: 'Thiếu thừa kỳ',
        dataIndex: 'remainOverAmount',
        key: 'remainOverAmount',
        align: 'center',
        render: value => numberToMoney(value),
      },
      {
        title: 'Thiếu thừa cuối cùng',
        dataIndex: 'finalRemainOverAmount',
        key: 'finalRemainOverAmount',
        align: 'center',
        render: value => numberToMoney(value),
      },
      // {
      //   title: 'Thao tác',
      //   dataIndex: 'operation',
      //   className: 'action-column',
      //   align: 'center',
      //   render: () => <BtnView onClick={this.onViewRegistedCredits} />,
      // },
    ];

    const allowGetData =
      !!paramSearchClass.trainTypeId &&
      !!paramSearchClass.facultyId &&
      !!paramSearchClass.courseId &&
      !!paramSearchClass.semesterId &&
      !!paramSearchClass.yearEduId &&
      !!paramSearchClass.receivableIds &&
      paramSearchClass.receivableIds.length > 0;

    return (
      <Card title="DS lớp học">
        <CartHeadAction>
          <div className="btn-group">
            <BtnExport
              className="btnExport"
              title="Xuất báo cáo"
              disabled={isBtnExportDisable}
              onClick={() => this.doExport()}
              loading={downloading}
            />
          </div>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStudent />
          </Col>
        </Row>
        <StatisticBar totalInformation={totalInformation} totalStudents={totalStudents} />
        <Row style={{ marginTop: '10px' }}>
          <Col span={24}>
            <TableData
              reload={reload}
              service={this.getTuitionDeptChargeStatistic}
              paramSearch={paramSearchClass}
              columns={columns}
              allowGetData={allowGetData}
              rowKey="studentId"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TuitionDeptChargeStatisticPage />
  </Page>
);



