import { request, requestDownload } from 'utils';

const prefix = '/api/finance';

export const getTuitionDeptChargeStatistic = async ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  receivableIds,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/Receipt/BrowseFeeDebt`,
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      receivableIds,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const doExport = dataSearch =>
  requestDownload({
    defaultFileName: 'Nợ phí - lệ phí.pdf',
    prefix,
    url: '/Receipt/ExportReportFeeDebt',
    data: { ...dataSearch },
    method: 'POST',
  });



